export const ProConfColumns = [
  {
    title: "",
    dataIndex: "companyName"
  },
  {
    title: "",
    dataIndex: "itemName"
  },
  {
    title: "",
    dataIndex: "meterSum"
  },
  {
    title: "",
    dataIndex: "shedSum"
  },
  {
    title: "",
    dataIndex: "deviceSum"
  },
  {
    title: "",
    dataIndex: "isRefund",
    render: (item:any)=>WhetherType[item]
  },
  {
    title: "",
    maxWidth: 200,
    dataIndex: "powerFuseList",
    render(item:any[]) {
      return item.map((elem, i)=>(
      <Tag key={i}><span style={{color: FuseStatusColor[elem.fuseStatus]}}>{FuseStatus[elem.fuseStatus]}</span>
        {elem.unitTime}{elem.unitNum}</Tag>
      ))
    }
  },
  {
    title: "",
    maxWidth: 200,
    dataIndex: "powerRuleList",
    render(item:any[]) {
      return item.map((elem)=><Tag key={elem.id}>{PayType[elem.type]}{elem.unitMoney}/{elem.unitTime}</Tag>);
    }
  }
]